.feedback-form {
  padding-bottom: 34px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 88px;
    padding: 0 60px;
  }
}

.feedback-form__fieldset {
  .flex();
  padding: 0 20px;
  padding-bottom: 24px;
  margin-bottom: 24px;
  border: none;

  &:not(:last-of-type) {
    border-bottom: 1px solid #e5e5e5;

    @media (min-width: @tablet-width) {
      border: none;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 0;
  }
}

.feedback-form__wrapper {
  .flex();

  @media (min-width: @tablet-width) {
    display: grid;
    row-gap: 16px;
  }
}

.feedback-form__fieldset-impression {
  grid-column: span 2;
}

.feedback-form__wrapper--impression {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
    margin: 0 auto;
    column-gap: 50px;
    row-gap: 16px;
  }
}

.feedback-form__wrapper--places {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
    margin: 0 auto;
    column-gap: 50px;
    row-gap: 16px;
  }
}

.feedback-form__fieldset-places {
  grid-column: span 2;
}

.feedback-form__fieldset-freetext {
  grid-column: span 2;
}

.feedback-form__fieldset-name {
  .text();
  font-weight: 700;
  margin-bottom: 20px;
}

.feedback-form__item-wrapper {
  .flex();
  align-items: flex-start;

  &:not(:last-child) {
    margin-bottom: 16px;

    @media (min-width: @tablet-width) {
      margin-bottom: 0;
    }
  }
}

.feedback-form__button-wrapper {
  .flex();
  padding: 0 20px;
  align-items: center;

  @media (min-width: @tablet-width) {
    display: grid;
    padding: 0;
    grid-template-columns: repeat(2, 1fr);
    grid-column: span 2;
    align-items: center;
  }
}

.feedback-form__submit {
  padding: 0 48px;
}

.feedback-form__explanation {
  display: block;
  align-self: end;
  font-size: 12px;
  line-height: 21px;
  color: rgba(51, 51, 51, 0.6);

  @media (min-width: @tablet-width) {
    align-self: unset;
    justify-self: end;
  }
}
